.categories-list {

	font-family: @font-family-1;
	font-weight: @font-weight-normal;
	text-align: center;	

	@media (min-width: 768px) {

		display: flex;
      	justify-content: center;
      	align-items: center;
      	
	}

	&__item {

		display: block;
		margin: 20px 0;
		background-color: #fff;
		border: 1px solid #1ea590;
		text-decoration: none;
		color: @gray-darken;

		@media (min-width: 768px) {			
			width: 50%;
			margin-right: 20px;
		}

	}

	&__item:hover img {	filter: none; }

	&__item:last-child {

		@media (min-width: 768px) {	margin-right: 0; }

	}

	&__pic {
		
		height: 200px;
		overflow: hidden;

	}

	&__pic img {

		width: 100%;
		height: 100%;
		filter: grayscale(100%);
		transition: filter @transition;

	}

	&__price { padding: 46px 56px 50px 56px; }

	&__title { font-size: @font-size-lg-xx;	}

}

	.room__price {
		font-size: @font-size-base;
		font-weight: @font-weight-medium;		
	}

	.price__number { color: @secondary-color; }

