.reviews {
  padding: 68px 0 48px 0;
  font-family: @font-family-2;
  font-weight: @font-weight-regular;
  background-image: url("@{pic}reviewsbg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  &__inner {
    text-align: center;

    @media (min-width: 768px) {
      max-width: 480px;
      margin: 0 auto;
    }

    @media (min-width: 1200px) {
      max-width: 977px;
    }

  }
 
  .title--reviews {
    padding-bottom: 56px;
    color: #fff;
    font-weight: @font-weight-regular;
  }

  &__list {
    // padding-bottom: 50px;
    list-style-type: none;
  

    // @media (min-width: 1200px) {
    //   display: flex;
    //   justify-content: center;
    //   align-items: center;

    // }  
  }

&__item {
    font-size: @font-size-sm;
    margin-bottom: 40px;
    padding: 0 46px 30px 46px;
    background-color: #fff;

    @media (min-width: 768px) {     
        border: 1px solid #18ac93;
    }

    @media (min-width: 1200px) {         
          width: 50%;
          margin-right: 20px;
    }
}

// &__item:last-child {

//   @media (min-width: 768px) {
//     // margin-bottom: 40px;
//   }

// }

  &__pic { 
    margin-bottom: 9px; }

  &__pic-item {

    margin: 10px auto 0 auto;
    border: 2px solid #fff;

  }

  &__blockquote { 
    margin-top: 9px;
    margin-bottom: 13px;

    &-img {
      display: block;
      margin-top: 6px;
      margin-bottom: 6px;
    }

  }

  &__blockquote p {
    margin-top: 6px;
      min-height: 100px;
      overflow: hidden;
      font-size: @font-size-base;
  }

  &__cite {
    font-weight: @font-weight-medium;
    font-style: normal;
    margin-bottom: 6px;
  }

  &__date {
    margin-top: 13px;
    padding-top: 14px;
  }

  &__date p {
    font-size: @font-size-sm;
  }      

  .btn--reviews {}

}